import { makeCSVtoJSON } from '../dataAssemble';

const Reviews = () => {
    const courses =  makeCSVtoJSON("/datas/courses.csv");
    const reviews =  makeCSVtoJSON("/datas/reviews.csv");

    let getData;
    reviews.then(reviewData => {
        courses.then(coursesData => {
            for (let oneReview of reviewData) {
                for (let i in coursesData) {
                    let isSameCode = coursesData[i].classCode == oneReview.classCode

                    if(isSameCode) {
                        oneReview.className = coursesData[i].className;
                        break;
                    } else if (!isSameCode && coursesData.length - 1 == i) {
                        oneReview.className = '알 수 없는 수업';
                    }
                }
            }
        })
        // console.log(reviewData);
        getData = reviewData;
    });

     return(
        <ul className='reviewList'>
            <li>
                <div>
                    <div></div>
                </div>
                <ul>
                    <li>TOEIC 700</li>
                    <li>nams9****</li>
                    <li>제목</li>
                    
                    <li>
                        2025-06-01
                    </li>
                        <li>
                        <i className="fa-solid fa-star"></i>
                        <i className="fa-solid fa-star"></i>
                        <i className="fa-solid fa-star"></i>
                        <i className="fa-solid fa-star"></i>
                    </li>
                </ul>
            </li>


        </ul>
    );
}

