import axios from "axios"

function arrayFromOneCSVRow(oneRowCSV) {
    const oneArray = new Array();
    let elementStart = 0;

    for (let i in oneRowCSV) {
        if (oneRowCSV[i]==',') {
            oneArray.push(oneRowCSV.substring(elementStart, i))
            elementStart = (Number(i) + 1);
        } else if (i == oneRowCSV.length - 1) {
            oneArray.push(oneRowCSV.substring(elementStart));
        }
    }
    return oneArray;
}

async function makeCSVtoJSON(csvURl) {
    const response = await axios.get(csvURl).then(result => {
        const arrayJSON = new Array();
        let contents = result.data;
        let indexArray = new Array();
        let startAssemble = false;

        do {
            const inputRow = arrayFromOneCSVRow(contents.substring(0, contents.indexOf('\r\n')));
            if (indexArray.length == 0 && !startAssemble) {
            startAssemble = true;

            } else if (indexArray.length == 0 && startAssemble) {
            indexArray = inputRow;

            } else {
            let oneCourse  = '{'
            for (let i in indexArray) {
                oneCourse += '"' + indexArray[i] + '":"' + inputRow[i] + '"';
                if (i < indexArray.length - 1) {
                oneCourse +=',';
                }
            }
            oneCourse += '}';
            
            arrayJSON.push(JSON.parse(oneCourse));
            } 
            
                contents = contents.substring(contents.indexOf('\r\n') + 2);
        } while (contents.length > 0);
        
        return arrayJSON;
    
    }).catch(error => {console.log(error)});

    return response;
}

