import { Container, Nav, Navbar} from 'react-bootstrap';
import { Link } from 'react-router-dom';

import logoBlue from "../images/project4logoB.png";
import sns1 from "../images/facebook.png";
import sns2 from "../images/instagram.png";
import sns3 from "../images/naverblog.png";
import sns4 from "../images/kakaotalk.png";

const Header = () => {
    return(
        <header>
            
            <div id='headerTop'>
                <Container>
                    <div className='top1'>
                        <Link to='/member/login'>로그인</Link>
                        <Link to='/member/join'>회원가입</Link>
                        <Link to='/member/mypage'>마이페이지</Link>
                        <Link to='/member/cart'>장바구니</Link>
                        <Link to='/helpdesk'>고객센터</Link>
                    </div>
                    <div className='top2'>
                        <Link to='#'><img src={sns1}/></Link>
                        <Link to='#'><img src={sns2}/></Link>
                        <Link to='#'><img src={sns3}/></Link>
                        <Link to='#'><img src={sns4}/></Link>
                    </div>
                </Container>
            </div>
            
            <Navbar collapseOnSelect expand="md" id='headerMenu'>
                <Container>
                <Navbar.Brand href="/">
                    <img src={logoBlue}/>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav>
                    <Nav.Link href="/application">수강 신청</Nav.Link>
                    <Nav.Link href="/courses">강좌 안내</Nav.Link>
                    <Nav.Link href="/tests">시험 안내</Nav.Link>
                    <Nav.Link href="/services">학생 지원</Nav.Link>
                    <Nav.Link href="/community">커뮤니티</Nav.Link>
                    <Nav.Link href="/introduction">어학원소개</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
                </Container>
            </Navbar>
        </header>
    );
}

export default Header;