import { Table } from "react-bootstrap";

const Calendar = (props) => {
    const {calendarPurpose} = props;
    
    let today = new Date();
    let todayForm = (today.getMonth() + 1) + '/' + (today.getDate());
    const listForCalendar = new Array();

    if (calendarPurpose == 'mainpage') {
        
        let inputDate = new Date();
        let todayName = today.getDay();
        
        if(todayName > 0) {
            inputDate.setDate(today.getDate() - todayName - 1);
        }

        for (let i = 0; i < 3; i++) {
            const listForWeek = new Array();
            for (let j = 0; j < 7; j++) {
                inputDate.setDate(inputDate.getDate() + 1)
                let inputForm = (inputDate.getMonth() + 1) + '/' + (inputDate.getDate());
                listForWeek.push(inputForm);
            }
            listForCalendar.push(listForWeek);
        }

    }
    
    return(
        <Table>
            <thead>
                <tr>
                    <th>Sun</th>
                    <th>Mon</th>
                    <th>Tue</th>
                    <th>Wed</th>
                    <th>Thr</th>
                    <th>Fri</th>
                    <th>Sat</th>
                </tr>
            </thead>
            <tbody>
                {
                    listForCalendar.map((weeks, i) => 
                        <tr key={i}>
                            {weeks.map((dates, j) => 
                                <td key={j}>
                                    <p className={
                                        dates == todayForm ? 'today':''
                                    }>
                                        {dates}
                                    </p>

                                    {
                                    i % 2 == 0 && j == 5?

                                    <p className="scheduleEng">모의토익</p>:null
                                    }
                                    <p className="scheduleEng">모의토익</p>
                                    <p className="scheduleEng">모의토익</p>
                                </td>)
                            }
                        </tr>
                    )
                }
            </tbody>
        </Table>
    );
}

export default Calendar;